"use client";

import { useConvexAuth, useQuery } from "convex/react";
import { api } from "@/convex/_generated/api";
import { Doc } from "@/convex/_generated/dataModel"; // Importando Doc para tipagem
import { format } from "date-fns";
import { ptBR } from "date-fns/locale";
import { Calendar, CheckCircle } from "lucide-react";

export default function DashboardPage() {
  const { isLoading, isAuthenticated } = useConvexAuth();

  if (isLoading) {
    return <div className="flex items-center justify-center h-screen">Carregando...</div>;
  }

  if (!isAuthenticated) {
    // O middleware deve redirecionar, mas é uma segurança extra
    return <div className="flex items-center justify-center h-screen">Por favor, faça login para ver seu painel.</div>;
  }

  return <MemberDashboard />;
}

function MemberDashboard() {
  // Busca o usuário atual para obter o nome e o terreiroId
  const currentUser = useQuery(api.users.getMe);
  const terreiroId = currentUser?.terreiroId;

  // Busca os diagnósticos e eventos
  // Usamos 'skip' para não rodar a query de eventos antes de ter o terreiroId
  const myDiagnoses = useQuery(api.diagnoses.getMyDiagnosesWithDetails) || [];
  const publicEvents = useQuery(api.events.getPublic, terreiroId ? { terreiroId } : "skip") || [];

  if (!currentUser) {
    return <div className="flex items-center justify-center h-screen">Carregando dados do usuário...</div>;
  }
  
  // Filtra diagnósticos que não puderam encontrar o evento (ex: evento deletado)
  const validDiagnoses = myDiagnoses.filter(d => d.eventDetails);

  return (
    <div className="container mx-auto p-4 md:p-8 space-y-12">
      <div>
        <h1 className="text-3xl font-bold">Meu Painel</h1>
        <p className="text-muted-foreground">Bem-vindo, {currentUser.name}!</p>
      </div>

      {/* Seção de Diagnósticos */}
      <section>
        <h2 className="text-2xl font-semibold mb-4 border-b pb-2">Minhas Tarefas e Diagnósticos</h2>
        <div className="space-y-4">
          {validDiagnoses.length > 0 ? (
            validDiagnoses.map((diag) => (
              <div key={diag._id} className="p-4 bg-yellow-50 border border-yellow-200 rounded-lg flex items-start space-x-4">
                <CheckCircle className="h-6 w-6 text-yellow-600 mt-1 flex-shrink-0" />
                <div>
                  <h3 className="font-bold">{diag.eventDetails!.title}</h3>
                  <p className="text-sm text-gray-700">{diag.eventDetails!.description}</p>
                  {diag.notes && <p className="text-sm mt-1 italic text-gray-600"><strong>Observação:</strong> {diag.notes}</p>}
                  <div className="flex items-center text-sm text-gray-500 mt-2">
                    <Calendar className="h-4 w-4 mr-2" />
                    <span>{format(new Date(diag.eventDetails!.date), "dd 'de' MMMM, yyyy", { locale: ptBR })}</span>
                  </div>
                </div>
              </div>
            ))
          ) : (
            <p className="text-gray-500">Você não tem nenhuma tarefa ou diagnóstico no momento.</p>
          )}
        </div>
      </section>

      {/* Seção de Eventos Públicos */}
      <section>
        <h2 className="text-2xl font-semibold mb-4 border-b pb-2">Agenda do Terreiro</h2>
        <div className="space-y-4">
          {publicEvents.length > 0 ? (
            // CORREÇÃO: Adicionando o tipo explícito para 'event'
            publicEvents.map((event: Doc<"events">) => (
              <div key={event._id} className="p-4 bg-gray-50 border rounded-lg flex items-start space-x-4">
                <Calendar className="h-6 w-6 text-gray-600 mt-1 flex-shrink-0" />
                 <div>
                    <h3 className="font-bold">{event.title}</h3>
                    <p className="text-sm text-gray-700">{event.description}</p>
                    <div className="flex items-center text-sm text-gray-500 mt-2">
                      <span>{format(new Date(event.date), "dd 'de' MMMM, yyyy", { locale: ptBR })}</span>
                    </div>
                  </div>
              </div>
            ))
          ) : (
            <p className="text-gray-500">Nenhum evento público agendado no momento.</p>
          )}
        </div>
      </section>

    </div>
  );
}

